.stats_table {
    border-collapse: separate;
    border-spacing: 0 8px;
    width: 100%;
  }

  .stats_table > tbody > tr > td,
.stats_table > tbody > tr > th,
.stats_table > tfoot > tr > td,
.stats_table > tfoot > tr > th,
.stats_table > thead > tr > td,
.stats_table > thead > tr > th {
  border: none !important;
}

.stats_table tbody td:first-child {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  text-align: left !important;
  border: 1px solid rgb(153, 152, 152) !important;
  border-right: none !important;
  
}
.stats_table tbody td:last-child {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  border: 1px solid rgb(153, 152, 152) !important;
  border-left: none !important;
}

.stats_table thead tr th:first-child{
    margin-right: 5%;
}

.stats_table tbody tr {
  margin-top: 15px;
  border-radius: 3px !important;
  -webkit-backdrop-filter: blur(65px);
  backdrop-filter: blur(65px);
  border: 1px solid rgb(153, 152, 152)!important;
padding: 10px;
 
}
.stats_table tbody tr td {
    padding: 5px;
