

.loading {
--border-width: 10px;
height: 30px;
width: 30px;
border-radius: 50%;
--mask: radial-gradient(
farthest-side,
transparent calc(100% - var(--border-width) - 0.5px),
#000 calc(100% - var(--border-width) + 0.5px)
);
-webkit-mask: var(--mask);
mask: var(--mask);
background: linear-gradient(to top, rgba(96, 83, 132, 1), rgba(15, 8, 36, 1))
100% 0/50% 100% no-repeat,
linear-gradient(rgba(96, 83, 132, 0.5) 50%, transparent 95%) 0 0/50% 100%
no-repeat;
animation: spin 1s linear infinite;
margin-left: 45%;
margin-bottom: 4%;
  
}
