@keyframes dropdown {
  0% {
    top: 0;
  }
  100% {
    top: 100%;
  }
}
@keyframes dropup {
  0% {
    opacity: 1;
    top: 100%;
  }
  100% {
    opacity: 0;
    top: 0;
  }
}
.StatusdropDown_content_active {
  top: 100%;
  display: block;
  /* position: absolute; */
